h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
a {
  color: #ccc;
  background-color: transparent;
  text-decoration: none;
  &:hover,
  &:focus,
  &:active {
    color: #fff;
  }
}
ul {
  list-style: none;
  overflow: hidden;
}

.container {
  max-width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  @media (min-width: 576px) {
    max-width: 540px;
  }

  @media (min-width: 768px) {
    max-width: 720px;
  }

  @media (min-width: 992px) {
    max-width: 960px;
  }

  @media (min-width: 1200px) {
    max-width: 1140px;
  }
}

.page-content {
  position: relative;
  margin: 0 auto;
  min-height: calc(100vh - 80px);
}

.row {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}
.col-5 {
  flex: 0 0 100%;
  max-width: 100%;
  margin-right: 60px;
  margin-bottom: 50px;
  @media (min-width: 768px) {
    flex: 0 0 230px;
    max-width: 230px;
  }
}

.col-6 {
  flex: 0 0 50%;
  max-width: 50%;
}

.col-7 {
  flex: 0 0 100%;
  max-width: 100%;
  @media (min-width: 768px) {
    flex: 0 0 58%;
    max-width: 58%;
  }
  @media (min-width: 99268px) {
    flex: 0 0 65%;
    max-width: 65%;
  }
}

.btn {
  display: inline-block;
  font-weight: 400;
  color: #fff;
  background-color: #007bff;
  border: 1px solid #007bff;
  text-align: center;
  vertical-align: middle;
  -webkit-user-select: none;
  user-select: none;
  padding: 6px 12px;
  font-size: 16px;
  line-height: 1.5;
  border-radius: 3px;
  box-shadow: none;
  cursor: pointer;
}
