.slider {
  -webkit-appearance: none;
  width: 100%;
  height: 15px;
  border-radius: 5px;
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
  &::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: rgb(0, 123, 255);
    cursor: pointer;
  }
  &::-moz-range-thumb {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: rgb(0, 123, 255);
    cursor: pointer;
  }
}
.price-range-indicator {
  justify-content: space-between;
}
.flights-row {
  margin: 70px 0;
  .filter-title {
    font-size: 30px;
    margin-bottom: 20px;
  }
}

.journey-type {
  margin: 0;
  padding: 0;
  li {
    display: inline-block;
  }
  .journey {
    color: #000;
    padding: 7px 15px;
    display: inline-block;
    border-radius: 3px;
    margin-right: 5px;
    &.active {
      color: #fff;
      background-color: rgb(0, 123, 255);
    }
  }
}
.button-wrapper {
  display: flex;
  align-items: center;
}
.searched-for {
  background: #f0f0f0;
  padding: 10px 20px;
  margin-bottom: 10px;
}
.reset-search {
  color: #007bff;
  margin-left: 12px;
  &:hover,
  &:active,
  &:focus {
    color: rgb(0, 123, 255);
  }
}
.refine-search {
  margin-top: 20px;
  margin-bottom: 20px;
}
.search-form {
  border-bottom: 1px solid #ccc;
  padding-bottom: 20px;
  .form-group {
    margin: 10px 0;
  }
  .form-control {
    display: block;
    width: 300px;
    height: calc(1.25em + 0.75rem + 2px);
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: 0.25rem;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
    &:focus {
      color: #495057;
      background-color: #fff;
      border-color: #80bdff;
      outline: 0;
      box-shadow: 0 0 0 2px rgb(0, 123, 255);
    }
    @media (min-width: 768px) {
      width: 200px;
    }
  }
}
