.header {
  min-height: 80px;
  background-color: #000;
  box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, 0.1);
  width: 100%;
  z-index: 3;
  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    overflow: hidden;
    @media (min-width: 768px) {
      display: flex;
      justify-content: flex-end;
    }
  }
  li a {
    display: block;
    padding: 18px 12px;
    text-decoration: none;
    @media (min-width: 768px) {
      padding-top: 28px;
      padding-bottom: 28px;
    }
  }
  .logo {
    display: block;
    float: left;
    font-size: 2em;
    padding: 18px 0 20px;
    text-decoration: none;
  }
  .menu {
    max-height: 0;
    width: 100%;
    transition: max-height 0.2s ease-out;
    &.show {
      max-height: 100%;
    }
    @media (min-width: 768px) {
      max-height: none;
      width: auto;
    }
  }
  .menu-icon {
    cursor: pointer;
    display: inline-block;
    float: right;
    padding: 40px 20px;
    position: relative;
    user-select: none;
    .navicon {
      background: #fff;
      display: block;
      height: 2px;
      position: relative;
      transition: background 0.2s ease-out;
      width: 18px;
      &:before {
        top: 5px;
      }

      &:after {
        top: -5px;
      }
      &:before,
      &:after {
        background: #fff;
        content: "";
        display: block;
        height: 100%;
        position: absolute;
        transition: all 0.2s ease-out;
        width: 100%;
      }
    }
    @media (min-width: 768px) {
      display: none;
    }
  }
}
